import React, { Component } from 'react'
import {
  Router,
  Switch,
  Route,
  Link
} from 'react-router-dom'
import Cover from './components/cover'
import CableInfoWrapper from './components/cableInfoWrapper'
import CableSelector from './components/cableSelector'
// import Dealers from './components/dealers'
import Press from './components/press'
import ContactUs from './components/contactUs'
import Footer from './components/footer'
import ScrollToTop from './components/scrollToTop'
import { cables } from './constants'
import { history } from './helpers'

class App extends Component {
  constructor (props) {
    super(props)
    this.state = {
      loaded: false,
    }
    this.scrollRefs = {
      home: React.createRef(),
      press: React.createRef(),
      contactUs: React.createRef()
    }
    cables.forEach((item, idx) => {
      this.scrollRefs[`cable${idx}`] = React.createRef()
    });
  }

  componentDidMount () {
    history.listen((location) => {
      
      console.log(location.pathname)
  });
  }

  render() {
    return(
      <Router history={history}>
        <Cover refs={this.scrollRefs}/>
        <CableSelector refs={this.scrollRefs}/>
        <CableInfoWrapper refs={this.scrollRefs}/>
        <Press scrollRef={this.scrollRefs.press}/>
        <ContactUs scrollRef={this.scrollRefs.contactUs}/>
        <Footer/>
        <ScrollToTop scrollRef={this.scrollRefs.home}/>
      </Router>
    )
  }
}

function ScrollTo(props) {
  if(props.scrollRefs && props.toComonent && props.loaded) {
    if(props.scrollRefs[props.toComonent] && props.scrollRefs[props.toComonent].current) {
      const ref = props.scrollRefs[props.toComonent]
      window.scrollTo({
        top: ref.current.offsetTop,
        left: 0,
        behavior: 'smooth'
      })
    } else {
      return null
    }
  } else {
    return null
  }
}

export default App
