import React, { Component } from 'react'
import injectSheet from 'react-jss'
import { colors } from '../constants'
import { mediaWidth, gridColumnTemplate } from '../helpers'

const styles = {
  container: {
    width: '100%',
    display: 'grid',
    height: 'auto',
    paddingBottom: 50,
    ...gridColumnTemplate(),
    gridTemplateRows: '100px 1fr 1fr 10px auto',
    ...mediaWidth(768, {
      gridTemplateRows: '50px auto auto 10px 1fr auto',
    })
  },
  c: {
    background: colors.black,
  },
  cR: {
    background: `linear-gradient(35deg, ${colors.gradientDark} -20%, ${colors.gradientLight} 100%)`,
  },
  background: {
    gridColumn: '1 / -1',
    gridRow: '1 / -1',
  },
  textLeft: {
    gridColumn: '2 / -8',
    marginRight: 40, 
    ...mediaWidth(768,{
      margin: 'auto',
      width: '100%',
      gridColumn: '2 / -2',
    }),
    '& h2::after': {
      background: `linear-gradient(12deg, ${colors.gradientDark} -100%, ${colors.gradientLight} 100%)`,
    },
  },
  textRight: {
    gridColumn: '8 / -2',
    marginLeft: 40,
    ...mediaWidth(768,{
      margin: 'auto',
      width: '100%',
      gridColumn: '2 / -2',
    }),
    '& h2::after': {
      background: colors.white,
    },
  },
  textContainer: {
    maxWidth: '100%',
    gridRow: '2 / 4',
    ...mediaWidth(768,{
      gridRow: '2 / 3',
    }),
    '& h2': {
      display: 'block',
      color: colors.white,
      fontSize: '1.2em',
      margin: '30px 0',
      ...mediaWidth(600,{
        fontSize: '1.6em',
      })
    },
    '& h2::after': {
      content: '""',
      display: 'block',
      position: 'absolute',
      width: 225,
      height: 5,
      marginTop: 15,
      ...mediaWidth(600,{
        height: 3,
        marginTop: 13
      })
    },
  },
  descTxt: {
    fontSize: '0.8em',
    color: colors.white,
    ...mediaWidth(600,{
      fontSize: '1.2em',
    })
  },
  bodyTxt: {
    fontSize: '0.6em',
    color: colors.white,
    ...mediaWidth(600,{
      fontSize: '1.0em',
    })
  },
  imgLeft: {
    boxShadow: '0px 0px 15px 1px rgba(0,0,0,0.3)',
    gridColumn: '2 / -8',
    ...mediaWidth(768,{
      margin: 'auto',
      gridColumn: '2 / -2',
    }),
  },
  imgRight: {
    boxShadow: '0px 0px 15px 1px rgba(255,255,255,0.3)',
    gridColumn: '8 / -2',
    ...mediaWidth(768,{
      margin: 'auto',
      gridColumn: '2 / -2',
    }),
  },
  image: {
    position: 'relative',
    border: `5px solid ${colors.white}`,
    borderRadius: 3,
    marginTop: 26,
    maxWidth: 'calc(100% - 20px)',
    gridRow: '2 / 3',
    ...mediaWidth(768,{
      gridRow: '3 / 4'
    }),
  },
  miniImgCont: {
    gridRow: '3 / 4',
    maxWidth: '100%',
    ...mediaWidth(768,{
      gridRow: '5 / 6'
    }),
    '& img': {
      cursor: 'pointer',
      width: 'calc((100% - 60px) / 3)',
      margin: '10px 10px 10px 0',
      border: `5px solid ${colors.white}`,
      borderRadius: 3,
      ...mediaWidth(768,{
        margin: '10px 5px',
      }),
    },
    '& img:last-child': {
      marginRight: '0px !important',
    }
  },
  miniLeft: {
    '& img': {
      boxShadow: '0px 0px 15px 1px rgba(0,0,0,0.3)',
    },
    gridColumn: '2 / 8',
    ...mediaWidth(768,{
      margin: 'auto',
      gridColumn: '2 / -2',
    }),
  },
  miniRight: {
    '& img': {
      boxShadow: '0px 0px 15px 1px rgba(255,255,255,0.3)',
    },
    gridColumn: '8 / -2',
    ...mediaWidth(768,{
      margin: 'auto',
      gridColumn: '2 / -2',
    }),
  }
}

class CableInfoSection extends Component {
  constructor (props) {
    super(props);
    this.state = {
      selectedImage: null,
    }
    this.handleImageChange = this.handleImageChange.bind(this)
  }
  handleImageChange (img) {
    this.setState({selectedImage: img});
  }
  render() {
    const { selectedImage } = this.state;
    const { classes, cable, scrollRef } = this.props;
    return (
      <div 
        ref={scrollRef}
        className={`${classes.container} ${cable.sort % 2 !== 0
          ? classes.c : classes.cR}`}
      >
        <div className={classes.background}></div>
        <div
          className={`${classes.textContainer} ${cable.sort % 2 !== 0
            ? classes.textLeft : classes.textRight}`}
        >
          <h2>{cable.name}</h2>
          <p className={classes.descTxt}>{cable.description}</p><br/>
          {cable.info && cable.info.length && cable.info.length > 0 && cable.info.map(text =>{
            return(
            <p className={classes.bodyTxt}>{text}<br/><br/></p>
            )
          })}
        </div>
        <img
          ref={el => this.myRef = el}
          className={`${classes.image} ${cable.sort % 2 !== 0
            ? classes.imgRight : classes.imgLeft}`}
          src={selectedImage || cable.mainPicture}
          alt={`Bertram Audio - ${cable.name}`}
        />
        {cable.allPictures && cable.allPictures.length > 0 &&
          <div className={`${classes.miniImgCont} ${cable.sort % 2 !== 0
            ? classes.miniRight : classes.miniLeft}`}>
            {cable.allPictures.map((img, idx) => {
              return (
                <img onClick={() => this.handleImageChange(img)} key={idx} src={img} alt={`${cable.name} image ${idx}`}/>
              )
            })}
          </div>
        }
      </div>
    )
  }
}
export default injectSheet(styles)(CableInfoSection)