import React, { Component } from 'react'
import TopMenu from './topMenu'
import injectSheet from 'react-jss'
import { colors, cables } from '../constants'
import { mediaWidth, gridColumnTemplate } from '../helpers'

const styles = {
  container: {
    width: '100%',
    height: 'auto',
    display: 'grid',
    ...gridColumnTemplate(),
    gridTemplateRows: '350px',
    ...mediaWidth(768, {
      gridTemplateRows: 'auto',
    })
  },
  cablesBackground: {
    gridColumn: '1 / -1',
    gridRow: '1 / -1',
    background: `linear-gradient(35deg, ${colors.gradientDark} -20%, ${colors.gradientLight} 100%)`,
    display: 'flex',
    opacity: 0,
    transition: 'all ease-in-out 0.5s',
  },
  cableBgLoaded: {
    opacity: 1,
  },
  cables: {
    gridColumn: '2 / -2',
    gridRow: '1 / -1',
    display: 'flex',
    justifyContent: 'space-evenly',
    alignItems: 'center',
    ...mediaWidth(768, {
      flexDirection:'column'
    }),
    '&> div:nth-child(1)': {
      transitionDelay: '0.4s',
      ...mediaWidth(768, {
        marginTop: 60
      })
    },
    '&> div:nth-child(2)': {
      transitionDelay: '0.5s'
    },
    '&> div:nth-child(3)': {
      transitionDelay: '0.6s',
      ...mediaWidth(768, {
        marginBottom: 60
      })
    }
  },
  cableContainer: {
    position: 'relative',
    width: 300,
    height: 'auto',
    margin: 20,
    borderRadius: 5,
    cursor: 'pointer',
    boxShadow: '0px 0px 15px 1px rgba(0,0,0,0.3)',
    transform: 'translateY(50px)',
    opacity: 0,
    transition: 'all ease-in-out 0.3s',
    ...mediaWidth(768, {
      width: 200,
      minHeight: 180,
    }),
    '& p': {
      backgroundColor: colors.black,
      color: colors.white,
      textAlign: 'center',
      width: 'calc(100% - 20px)',
      padding: 10,
      fontWeight: 600,
      fontSize: '1.0em',
      borderRadius: '0px 0px 5px 5px',
      ...mediaWidth(768, {
        padding: 5,
        width: 'calc(100% - 10px)',
      }),
      ...mediaWidth(600, {
        fontSize: '1.4em',
      })
    }
  },
  img: {
    height: 150,
    borderRadius: '5px 5px 0px 0px',
    backgroundRepeat: 'no-repeat',
    backgroundSize: 'cover',
  },
  cableLoaded: {
    opacity: 1,
    transform: 'translateY(0)'
  },
}

class CableSelector extends Component {
  constructor (props) {
    super(props)
    this.state = {
      loaded: false
    }
    this.handleClick = this.handleClick.bind(this)
  }
  componentDidMount () {
    setTimeout(() => {
      this.setState({ loaded: true})
    }, 100);
  }
  handleClick (refName) {
    if(this.props.refs) {
      const ref = this.props.refs[refName]
      window.scrollTo({
        top: ref.current.offsetTop,
        left: 0,
        behavior: 'smooth'
      })
    }
  }
  render() {
    const { loaded } = this.state
    const { classes } = this.props
    return (
      <div
        className={classes.container}
      >
        <div className={`${classes.cablesBackground} ${loaded ? classes.cableBgLoaded: null}`}></div>
        <div className={classes.cables}>
          {cables.map((cable, idx) => {
            return(
              <div
                onClick={() => this.handleClick(`cable${idx}`)}
                key={cable.sort}
                className={
                  `${classes.cableContainer} ${loaded ? classes.cableLoaded: null}`
                }
              >
                <div style={cable.tnStyle} className={classes.img}/>
                <p>{cable.name}</p>
              </div>
            )
          })}
        </div>
      </div>
    )
  }
}
export default injectSheet(styles)(CableSelector)
