import React, { Component } from 'react'
import TopMenu from './topMenu'
import injectSheet from 'react-jss'
import { colors, cables } from '../constants'
import { mediaWidth, gridColumnTemplate } from '../helpers'

const styles = {
  container: {
    width: '100%',
    minHeight: '350px',
    height: 'calc(100vh - 350px)',
    display: 'grid',
    ...gridColumnTemplate(),
    gridTemplateRows: '70px 70px 1fr auto 1fr',
    ...mediaWidth(768, {
      height: 'calc(100vh - 350px)',
      gridTemplateRows: '20px 40px 1fr auto 1fr',
    })
  },
  header: {
    gridColumn: '1 / -1',
    gridRow: '1 / -1',
    background: `radial-gradient(circle, rgba(17,21,22,0.3) 0%, rgba(17,21,22,0.9) 100%), linear-gradient(45deg, rgba(79,216,211,0.6) 0%, rgba(49,95,225,0.6) 100%), url(${require('../assets/img/bgLarge.png')})`,
    backgroundRepeat: 'no-repeat',
    backgroundPosition: 'center top',
    backgroundSize: 'cover',
  },
  headerEffect: {
    opacity: 0.3,
    gridColumn: '1 / -1',
    gridRow: '1 / -1',
    display: 'flex',
    alignItems: 'flex-end'
  },
  bar: {
    background: '#fff',
  },
  logo: {
    width: '100%',
    gridColumn: '2 / span 2',
    gridRow: '2 / 3',
    opacity: 0,
    position: 'relative',
    top: -100,
    transition: 'all ease-in-out 0.5s',
  },
  logoLoaded: {
    opacity: 1,
    top: 0,
  },
  menu: {
    gridColumn: 'span 6 / -2',
    gridRow: '2 / 3',
    opacity: 0,
    marginTop: -100,
    transition: 'all ease-in-out 0.5s',
    ...mediaWidth(768, {
      zIndex: 2,
    })
  },
  menuLoaded: {
    opacity: 1,
    marginTop: 0,
  },
  quote: {
    gridColumn: '3 / -3',
    gridRow: '4 / -2',
    textAlign: 'center',
    color: colors.white,
    fontSize: '1.4em',
    fontWeight: 500,
    margin: '0px',
    transform: 'translateX(-20px)',
    opacity: 0,
    transition: 'all ease-in-out 0.5s',
    '& span': {
      color: colors.mainColor,
      fontFamily: 'raleway',
      fontWeight: 800
    }
  },
  quoteLoaded: {
    opacity: 1,
    transform: 'translateX(0)'
  },
  logoSvg: {
    fill: colors.mainColor,
  }
}

class Cover extends Component {
  constructor (props) {
    super(props)
    this.state = {
      loaded: false
    }
    this.handleClick = this.handleClick.bind(this)
  }
  componentDidMount () {
    setTimeout(() => {
      this.setState({ loaded: true})
    }, 100);
  }
  handleClick (refName) {
    if(this.props.refs) {
      const ref = this.props.refs[refName]
      window.scrollTo({
        top: ref.current.offsetTop,
        left: 0,
        behavior: 'smooth'
      })
    }
  }
  renderBars () {
    const { classes } = this.props
    const arr = []
    const amount = 128
    for (let i = 0; i < amount; i++) {
      const rand = Math.floor((Math.random() * (10 + (i/2))) + (i/3));
      arr.push(
        <div
          className={classes.bar}
          style={{ 
            height: `${rand}%`,
            width: `calc(100vw / ${amount})`
          }}
        ></div>
      )
    }
    return arr
  }
  render() {
    const { loaded } = this.state
    const { classes, refs } = this.props
    return (
      <div
        className={classes.container}
        ref={refs.home}
      >
        <div className={classes.header}></div>
        <div className={`${classes.logo} ${loaded ? classes.logoLoaded : null}`}><img height='100%' src={require('../assets/img/logo5.png')} alt='Bertram Audio Logo'/></div>
        <div className={`${classes.menu} ${loaded ? classes.menuLoaded : null}`}>
          <TopMenu refs={refs}/>
        </div>
        <p className={`${classes.quote} ${loaded ? classes.quoteLoaded: null}`}><span>High  quality</span> signal <span>cables</span> for your need.</p>
      </div>
    )
  }
}
export default injectSheet(styles)(Cover)
